@import url('https://fonts.cdnfonts.com/css/gabriola');
body{
  margin: 0px;
  padding: 0px;
  font-family: 'Trebuchet MS';
  background: #373737;
  scroll-behavior: smooth;
  
}
a{
  text-decoration: none; /* no underline */
}

.header{
  width: 100%;
  height: 90px;
  background: #232323;
  background-size: cover;
  display: flex;
  align-items: center;
  position: absolute;
  color: white;
  top: 0; 
  z-index: 10;
  padding: 0 20px;
  box-sizing: border-box;
  .logo{
    width: 35px;
    height: 50px;
    margin-top: 10px;
    margin-left: 100px;
    opacity: 0.7;
    transition:1s;
  }
  .logo:hover{
    opacity: 1;
    transition:  0.5s;
  }
  .titles{
    color: white; /* Add color for visibility */
    font-size: 20px;
    letter-spacing: 2px;
    initial-letter:inherit;
    text-align: center;
    align-items: center;
    display: flex; 
    margin: auto;
  }
  .titel,.desh{
    display: inline-block;
    margin: 1.05vw;
    color: rgb(153, 151, 151);
    transition: 0.5s ease-in-out;
    cursor: pointer;
    padding-top: 5px;
    padding-bottom: 5px;
    border-bottom: 3px solid rgba(255, 255, 255, 0.0);
  }
  .titel.active {
    color: rgb(221, 221, 221);
    border-bottom: 3px solid rgba(255, 255, 255);
  }
  .messangers{
    justify-content: center;
    align-items: center;
    display: flex;
    margin-right: 100px;
  }
  .instagram{
    width: 31px;
    opacity: 0.4;
    transition: 1s;
    margin-right: 15px;
  }
  .facebook{
    width: 38.5px;
    opacity: 0.4;
    transition: 1s;
  }
  .phone{
    width: 30px;   
    opacity: 0.4;
    transition: 1s;
  }
  .instagram:hover,.facebook:hover,.phone:hover{
    opacity: 1;
  }
  @media screen and (max-width: 768px) {
    .logo{
      width: 40px;
      height: 60px;
      margin-top: 0px;
      margin-left: 50px;
    }
    .messangers{ margin-right: 60px;}
    .phone{width: 36px;}
  }
  @media screen and (max-width: 570px) {
    .logo{
      width: 35px;
      height: 55px;
      margin-top: 35px;
      margin-left: 30px;
    }
    .titles{
      font-size: 22px;
    }
    
    .messangers{
      margin-right: 30px;
    }
    .phone{width: 33px;}
  }
  @media screen and (max-width: 600px) {
    .titles{
      padding-top: 10px;
      font-size: 40px;
      font-family: 'Gabriola', sans-serif;
    }
  }
}
.section{
  height: calc(100vh);
  font-size: 3em;
  scroll-snap-align: start;
}



.slideOne {
  width: 100%;
  height: 100vh;
  background-size: cover;
  display: flex;
  align-items: center;
  .text {
    color: #ffffff;
    margin-left: 5%;
    letter-spacing: 2px;
    z-index: 1;
    width: 100%;
  }

  .title {
    font-size: 30px;
    font-weight: 600;
    margin-top: 0vh;
    width: 60vw;
  }

  .information {
    font-size: 21px;
    padding-top: 5vh;
    font-family: sans-serif;
    width: 60vw; /* Ширина блока */
  }

  .profile {
    width: 32vw;
    height: 35vw;
    align-self: self-end;    
  }
  @media screen and (max-width: 2200px) {
    .information {width: 40vw;}
    .profile {
      width: 30vw;
      height: 36vw;
    }
    .text {width: 100%;}
  }
  @media screen and (max-width: 1800px) {
    .profile {
      width: 30vw;
      height: 36vw;
    }

  }
  @media screen and (max-width: 1700px) {
    .profile {
      width: 30vw;
      height: 36vw;
    }
    .information {width: 60vw;}
  }
  @media screen and (max-width: 1600px) {
    .profile {
      width: 30vw;
      height: 36vw;
    }
  }
  @media screen and (max-width: 1500px) {
    .profile {
      width: 31vw;
      height: 37vw;
    }
    
  }
  @media screen and (max-width: 1400px) {
    .profile {
      width: 32vw;
      height: 38vw;
    }
  }
  @media screen and (max-width: 1200px) {
    .profile {
      width: 31vw;
      height:40vw;
    }
    .text {width: 100%;}
  }
  @media screen and (max-width: 1100px) {
    .profile {
      width: 31vw;
      height: 40vw;
    }
  }
  @media screen and (max-width: 1000px) {

    .information{
      font-size: 21px;
      margin-top: 60px;
    }
    .profile {
      width: 40vw;
      height: 45vw;
    }
    .text {width: 58.5%;} 
    .information {width: 65vw;}
  }
