    -ms-user-select: none;
    user-select: none;
    margin:40% auto 30% auto;
    padding-left:0;

  }
  .text.phone {width: auto;}
  .title.phone {
    font-size: 25px;
    text-align: center;
    margin-top: 5vh;
    max-width: 100%; 
    width: auto;
  }
  .information.phone {
    font-size: 20px;
