}
.footer{

  background-image: url('../img/Footer.png');
  background-size: cover; /* Настройка размера фонового изображения */
  background-color: rgb(34, 34, 34);
  background-blend-mode: overlay; /* Смесь фонового изображения с цветом */
  display: flex;
  flex-direction: column;
  justify-content:  flex-end;
  min-height: 100vh;
  .title{
    display: flex;
    justify-content: center;
    align-items: end;

  }
  
  .content{
    justify-content: center;
    align-items: end;
    margin: auto;    
  }
  .main,.base{
    color: rgb(202, 202, 202);
    font-size: 30px;
    display: flex;
    justify-content: center;
  }
  .main{ 
    padding-top: 70px;
  }
  .base{
    padding-top: 2vh;
    font-size: 21px;
    padding-left: 30vw;
    padding-right: 30vw;
    align-items: center;
    text-align: center;
    margin: auto;
  }
  .myContacts{
    margin: 0;
    font-size: 26px;
    color: rgb(221, 221, 221);
  }
  .contacts{
    display: flex;
    justify-content: center;
    font-size: 24px;
    color: white;
    p{
      padding:40px;
      padding-top: 20px;
      transition: 0.75s ease;
      font-weight: 600;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-size: 300% auto;
      animation: textShine 5s ease-in-out infinite alternate; 
    }
    p:hover{
      transform: scale(1.07); /* Увеличение текста на 20% */
    }

  }
  @media screen and (max-width: 600px){
    .myContacts{
      margin: 15px;
      font-size: 26px;}
    .contacts{
      font-size: 20px;
      margin-left: 30px;
      margin-right: 30px;
      .instagram,.facebook,.telegram{
        padding: 10px;
       }
   }  
  }
  @media screen and (max-width: 900px){
    .content{
      justify-content: center;
      align-items: end;
      margin: auto;   
    }
    .main{ 
      padding-top: 50px;
      font-size: 24px;
    }
    .base{
      font-size: 21px;
      padding-left: 30px;
      padding-right: 30px;
    } 
    .mark{
      font-size: 18px;
    }
  }
  .instagram{
    background: linear-gradient(
      to right,
      #fe44d3 20%,
      #e41549 30%,
      #fac145 70%,
      #ea62ff 80%
    );
  }
  .facebook{
    background: linear-gradient(
      to right,
      #4291ff 20%,
      #0033ff 30%,
      #569cf8fe 70%
    );
  }
  .telegram{
    background: linear-gradient(
      to right,
      #89f3ff 20%,
      #19f0ff 30%,
      #00a4f0 70%,
      #08f5e5 80%
    );
  }
  .waterMark{
    border-top: solid 3px rgba(95, 95, 95, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 11vh;
  }
  .mark{
    font-size: 20px;
    color: rgb(126, 126, 126);
    font-family: 'Tangerine', cursive;
    text-align: center;
    letter-spacing: 0.5px;
  }
  @media screen and (max-width: 900px){ 
    .mark{font-size: 14px;}
  }
}
.footer::before {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('../img/Footer.png');
  background-size: cover; /* Настройка размера фонового изображения */
  opacity: 0.5; /* Степень прозрачности изображения */
  z-index: -1; /* Чтобы изображение находилось позади текста */
}
@keyframes textShine {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
.information.active{
  color: #0033ff;
}